<template>
    <el-container v-loading="loading">
        <div class="rise x add">
            <div class="title">{{title}}</div>
            <div class="content">
                <div class="item" v-for="(item, index) in data" :key="index">
                    <span>{{item.name}}</span>
                    <!-- input -->
                    <div v-if="!item.type">
                        <el-input v-if="item.module === 'name'" v-model="name" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'contactPerson'" v-model="contactPerson" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'mobile'" v-model="mobile" :placeholder="item.placeholder"/>
                        <el-input v-if="item.module === 'memo'" v-model="memo" :placeholder="item.placeholder"/>
                    </div>
                    <!-- select -->
                    <div v-if="item.type === 'select'">
                        <el-select v-if="item.module === 'schoolId'" v-model="schoolId" placeholder="全部">
                            <el-option
                                v-for="item in item.options"
                                :key="item.id"
                                :label="item.dValue"
                                :value="item.id"
                            ></el-option>
                        </el-select>
                    </div>
                </div>
            </div>
            <div class="btn-list">
                <el-button @click="cancle()" class="btn">取消</el-button>
                <el-button @click="save()" class="btn">保存</el-button>
            </div>
        </div>
    </el-container>
</template>

<script>
import queryString from "querystring";
import { DateDay } from 'utils/formData';
import { getSchool } from 'data/listBody';
const titles = ["新建", "编辑"];
const tips = ['请选择学校'];
const defaultV = [
    { name:'校区名称：', placeholder:'请选择校区名称', module:'schoolId', type:'select'},
    { name:'班级名称：', placeholder:'请输入班级名称', module:'name'},
    { name:'联系人姓名：', placeholder:'请输入联系人姓名', module:'contactPerson'},
    { name:'联系人手机：', placeholder:'请输入联系人手机', module:'mobile'},
    { name:'备注：', placeholder:'请输入备注', module:'memo'},
]

export default {
    name: "add",
    data() {
        return {
            title: "",
            id: "",
            type:'',
            data: defaultV,           
            name:'',
            schoolId:'',
            contactPerson:'',
            mobile:'',
            memo:'',
            loading: false
        };
    },
    mounted() {
        this.dataInit();
        this.dropInit();
    },
    methods: {
        dropInit(){
            getSchool((res) => {
                let obj = null;
                obj = this.data.map(item => {
                    if(item.module === 'schoolId'){
                        item.options = res
                    }
                    return item;
                })
                this.data = obj;
            }, 'filter')        
        },
        dataInit() {
            const { type, id } = this.$route.query;
            this.type = type;
            this.id = id;
            if (type !== "edit") return (this.title = titles[0]);
            this.title = titles[1];
            this.detailAjax();
        },
        editInit(res){
            const { name, schoolId, mobile, contactPerson, memo } = res.data;
            this.name = name;
            this.schoolId = schoolId;
            this.mobile = mobile;
            this.contactPerson = contactPerson;
            this.memo = memo;
        },
        detailAjax() {
            //查看详情
            this.loading = true;
            this.api.get(`/class/${this.id}`).then((res)=>{
                this.loading = false;
                this.editInit(res);
            }).catch((err) => {
                console.log(err)
                this.loading = false;
                this.$message(err.message)
            })
        },
        editAjax(){
            const params = {
                name: this.name,
                mobile: this.mobile,
                schoolId: this.schoolId,
                contactPerson: this.contactPerson,
                memo: this.memo,
            }
            if(!this.schoolId){
                return this.$message(tips[0])
            }
            const axiosType = ['post','put'];
            let type = axiosType[0];
            if (this.type === "edit"){
                params.id = this.id;
                type = axiosType[1]
            }
            this.loading = true;       
            this.api[type](`/class`, params, 'form').then((res)=>{
                this.loading = false;
                this.$router.go(-1);
            }).catch((err) => {
              console.log(err)
              this.$message(err.message)
            })
        },
        save() {
            this.editAjax();
        },
        cancle() {
            //取消
            this.$router.go(-1);
        }
    }
};
</script>
 
<style lang="less" scoped>
</style>
 